@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

body {
  box-sizing: border-box !important;
  font-family:'Poppins', sans-serif;
}



.App {
  text-align: center;
}

.signOutBtn{
  display: flex;
  justify-content: flex-end;
  position: fixed !important;
  top: 0;
  width: 100%;
  z-index: 995;
}

main {
  position:relative;
  height: 100%;
  overflow: auto;
  overflow-x: hidden;
  padding: 70px 5px;
  background: rgb(191,99,228);
  background: -moz-linear-gradient(0deg, rgba(191,99,228,1) 41%, rgba(166,71,228,1) 52%, rgba(156,40,228,1) 66%);
  background: -webkit-linear-gradient(0deg, rgba(191,99,228,1) 41%, rgba(166,71,228,1) 52%, rgba(156,40,228,1) 66%);
  background: linear-gradient(0deg, rgba(191,99,228,1) 41%, rgba(166,71,228,1) 52%, rgba(156,40,228,1) 66%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#bf63e4",endColorstr="#9c28e4",GradientType=1);
}

footer{
  background-color: #dfeeff;
  padding: 4px;
  position: fixed;
  bottom: 0;
  width: 100%;
  background: rgb(191,99,228);
}

.buttonwrap{
  display: flex;
  padding: 25px;
  
  flex-direction: column;
  gap:60px;
}

.loginbtn{
  height: 100vh;
  align-content: center;
  background-image: url('./components/Untitled-2-01.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  gap:60px;
  flex-direction: column;
  z-index: 999;
}

input {
  line-height: 1.5px;
  width: fit-content;
  font-size: 1.5rem;
  background: rgb(255, 255, 255);
  color: white;
  outline: none;
  border: none;
  padding:10px;
  border-radius: 35px;
}

button4{
  color:#ffffff;
  font-family:'Poppins', sans-serif;
  font-weight: 500;
  font-size:25px;
  cursor:pointer;
}

button4:hover  {
  color:#ffffff;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  text-shadow: 0 3px 8px #7D5AB2;
  color: rgb(191, 99, 228);
}

button3 {
  text-align: left;
  background-color:#7D5AB2;
  border-radius:35px;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-weight: 500;
  font-size:25px;
  padding:15px 110px 15px 94px;
  text-decoration: none; 
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .2);
  
}

button3 .google-icon-svg{
  position: absolute;
  margin-top: 3px;
  margin-left: 9px;
  width: 32px;
  height: 32px;
 
  
}

button3:hover  {
background-color:#7D5AB2;
box-shadow: 0 3px 8px #7D5AB2;
transform: translate3d(0, 0, 0);
perspective: 1000px;
background: rgb(191, 99, 228);
}

button3:hover .google-icon-svg{
 
  transform: translate3d(0, 1px, 0);
  perspective: 1000px;
 
  }

button3:active {
position:relative;
}

button3:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/*end of button 3*/

button2 {
  background-color:#7D5AB2;
  border-radius:35px;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-weight: 500;
  font-size:25px;
  padding:15px 75px;
  text-decoration:none;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .2);
  }


button2:hover {
  background-color:#7D5AB2;
  box-shadow: 0 3px 8px #7D5AB2;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  background: rgb(191, 99, 228);
  }
  
  
button2:active {
  position:relative;
  top:1px;
  }
  
button2:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

/*End of button 2*/

button1 {
    font-family:'Poppins', sans-serif;;
    font-size:25px;
    padding:4px 10px;
    text-decoration:none;
}
  
.sign-in {
  color: #282c34;
  background: white;
  max-width: 400px;
  
}

ul, li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
  
}

.sent p {
  color: black;
  background: #ada5f5;
  align-self: flex-end;

  
}
.received p {
  background: #fdb5b5;
  color: black;

}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}

.rocket{
display: flex;
margin: 0 0;

} 

.rocket:hover{
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

.smiley{
  width: 30px;
  height: 30px;
  margin:0 0;
}

.smiley:hover{
  filter: drop-shadow(0 0 0.75rem rgb(248, 88, 248));
  
}

textarea {
  overflow: auto;
  resize: none;
  padding-top: 9px;
  padding-left: 20px;
}

.message-box{
  flex-direction: row;
  display: flex;
  height: 4.5vh;
  padding: 10px;
  position: relative;
  gap:5px;
}

.message-box .message-input {
    order:2;
    background: none;
    border: none;
    outline: none !important;
    color: black;
    font-family:'Poppins', sans-serif;;
    font-size:15px;
    margin: 0;
    border-radius: 35px;
    width: 98%;
    background-color: #ffffff;
    
}

.message-box .message-submit {
  order: 4;
  z-index: 1;
  cursor: pointer;
  border-radius: none;
  background-color: transparent;
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  border:none;
  
}

.buttonwrapper:hover{
  background-color:transparent;
  border-radius: 50%;
  background-color: none;
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}


/*Emoji CSS */
.message-box .emoji-icon {
  order: 3;
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  cursor: pointer;
  align-self: center;
  z-index: 999;
  position: relative;
  border: none;
  outline: none !important;
  background-color: transparent;
  
}

.emoji-picker {
  position: absolute;
  bottom: 75px;
  right: 30px;
  z-index: 998;
}

.emoji-picker-react input.emoji-search {
	width: calc(100% - 30px);
  height: 35px;
	margin-left: 15px;
	outline: none;
	box-shadow: none;
	padding: 10px;
	box-sizing: border-box;
	border: 1px solid #efefef;
	border-radius: 3px;
	transition: border 0.1s;
}

.emoji-picker-react ul.skin-tones-list label {
  height: 10px;
  width: 10px;
  padding: 0;
  display: block;
  cursor: pointer;
}

.logoutlogo{
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .2);
  width: 52px;
  height: 52px;
}

.logoutlogo:hover {
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

.logoutlogo:active {
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}
